<template>
    <div class="box pd-0">
        <b-modal :active.sync="selectingWand">
            <div class="box">
                <div class="picker" v-for="val in wands" :key="val.name" @click="selectWand(val)">
                    <WandSlot
                        :class="{isActiveWand: val.name == stats['sprite'].name}"
                        :value="val"
                    />
                </div>
            </div>
        </b-modal>
        <span class="label">Wand Sprite</span>
        <WandSlot class="mb-2" @click.native="selectingWand = true" :value="stats['sprite']" />
        <div class="field">
            <b-switch v-model="stats['shuffle']">
                <span class="label">{{ stats['shuffle'] ? "Shuffle Yes" : "Shuffle No" }}</span>
            </b-switch>
        </div>

        <b-field :label="'Spells/Cast: ' + stats['spellsCast']">
            <b-slider
                size="is-small"
                :min="1"
                :max="stats['capacity']"
                v-model="stats['spellsCast']"
            ></b-slider>
        </b-field>

        <b-field :label="'Speed: ' + stats['speed']">
            <b-slider size="is-small" :step="0.01" :min="0.01" :max="9.99" v-model="stats['speed']"></b-slider>
        </b-field>

        <b-field :label="'Cast delay: ' + stats['cast'] + 's'">
            <b-slider size="is-small" :step="0.01" :min="-9.99" :max="9.99" v-model="stats['cast']"></b-slider>
        </b-field>

        <b-field :label="'Recharge time: ' + stats['recharge'] + 's'">
            <b-slider
                size="is-small"
                :step="0.01"
                :min="-9.99"
                :max="9.99"
                v-model="stats['recharge']"
            ></b-slider>
        </b-field>

        <b-field :label="'Spread: ' + stats['spread'] + ''">
            <b-slider size="is-small" :min="-999" :max="999" v-model="stats['spread']"></b-slider>
        </b-field>

        <b-field label="Mana">
            <b-numberinput :min="1" :max="9999" controls-position="compact" :controls="false" v-model="stats['mana']"></b-numberinput>
        </b-field>

        <b-field label="Mana recharge">
            <b-numberinput :min="1" :max="9999" controls-position="compact" :controls="false" v-model="stats['mana_recharge']"></b-numberinput>
        </b-field>

        <span class="label">Always Cast</span>
        <draggable v-model="alwaysCast" :group="spellsGroup" :swap="true">
            <div
                v-for="(val, index) in alwaysCast"
                :key="keys[index]"
                @dblclick="removeAlwaysCast()"
            >
                <SpellSlot :value="alwaysCast[index]" />
            </div>
        </draggable>

        <span class="label">Spells Capacity: {{stats.capacity}}</span>
        <div class="buttons">
            <b-button
                :disabled="canAdd"
                @click="addCapacity"
                type="is-primary"
                size="is-small"
                icon-right="plus"
            ></b-button>
            <span class="label"></span>
            <b-button
                :disabled="canRemove"
                @click="removeCapacity"
                type="is-primary"
                size="is-small"
                icon-right="minus"
            ></b-button>
        </div>

        <draggable v-model="deck" :group="spellsGroup" :swap="true">
            <div
                class="picker"
                v-for="(val, index) in deck"
                :key="keys[index]"
                @dblclick="removeSpell(index)"
            >
                <SpellSlot :value="deck[index]" />
            </div>
        </draggable>
        <b-modal :active.sync="saving">
            <div class="box">
                <b-field label="Name">
                    <b-input v-model="stats['name']"></b-input>
                </b-field>
                <div class="column">
                    <div class="is-centered buttons">
                        <b-button
                            :loading="fakeSaving"
                            @click="save"
                            type="is-primary"
                            size="is-medium"
                        >Save</b-button>
                    </div>
                </div>
            </div>
        </b-modal>
        <div class="column">
            <div class="is-centered buttons">
                <b-button @click="saving = true" type="is-primary" size="is-medium">Save</b-button>
                <b-button @click="clear" type="is-primary" size="is-medium">Clear</b-button>
            </div>
        </div>
    </div>
</template>

<script>
import draggable from "vuedraggable";
import spellData from "../assets/spellData.json";
import wandsData from "../assets/wandsData.json";
import SpellSlot from "./SpellSlot.vue";
import WandSlot from "./WandSlot.vue";
export default {
    components: {
        draggable,
        WandSlot,
        SpellSlot
    },
    data() {
        return {
            fakeSaving: false,
            saving: false,
            selectingWand: false,
            stats: {
                name: "",
                sprite: {},
                speed: 1,
                shuffle: false,
                spellsCast: 1,
                cast: 0.1,
                recharge: 0.1,
                spread: 0,
                mana: 1300,
                mana_recharge: 280,
                capacity: 20
            },
            alwaysCast: [],
            deck: [],
            keys: [],
            spellsGroup: "spells",
            trashGroup: { name: "trash", pull: false },
            spells: spellData,
            wands: wandsData,
            spellCategory: 0
        };
    },
    computed: {
        canAdd() {
            return this.stats.capacity >= 30;
        },
        canRemove() {
            return this.stats.capacity <= 1;
        }
    },
    mounted() {
        this.$root.$on("addSpell", this.tryAddSpell);
    },
    beforeMount() {
        const urlData = this.$route.query.data;
        if (typeof urlData == "string") {
            let data = Buffer.from(urlData, "base64");
            data = JSON.parse(data);
            let valid = this.validateTiny(data);
            if (valid) {
                this.parseTiny(data);
            }
        } else {
            this.alwaysCast = [{}];
            let deck = [];
            for (let i = 0; i < this.stats.capacity; i++) {
                deck.push({});
            }
            this.deck = deck;
            let randomWand = Math.floor(Math.random() * this.wands.length);
            this.stats.sprite = Object.assign({}, this.wands[randomWand]);
        }
    },
    methods: {
        validateTiny(data) {
            let isOk = true;

            for (const key of data) {
                if (Array.isArray(key)) {
                    for (const subkey of key) {
                        if (typeof subkey !== "number") {
                            isOk = false;
                        }
                    }
                } else {
                    if (typeof key !== "number") {
                        isOk = false;
                    }
                }
            }
            return isOk;
        },
        parseTiny(data) {
            let wand = {
                stats: {},
                alwaysCast: [],
                deck: []
            };
            wand.stats.sprite = this.getWand(data[0][0]);
            wand.stats.speed = data[0][1];
            wand.stats.shuffle = Boolean(data[0][2]);
            wand.stats.spellsCast = data[0][3];
            wand.stats.cast = data[0][4];
            wand.stats.recharge = data[0][5];
            wand.stats.capacity = data[0][6];
            wand.stats.spread = data[0][7];
            wand.stats.mana = data[0][8];
            wand.stats.mana_recharge = data[0][9];

            wand.alwaysCast = data[1].map(this.getSpell);
            wand.deck = data[2].map(this.getSpell);
            this.stats = JSON.parse(JSON.stringify(wand.stats));
            this.alwaysCast = JSON.parse(JSON.stringify(wand.alwaysCast));
            this.deck = JSON.parse(JSON.stringify(wand.deck));
        },
        tryAddSpell(val) {
            for (let i in this.deck) {
                if (Object.keys(this.deck[i]).length === 0) {
                    this.$set(this.deck, i, val);
                    return;
                }
            }
        },
        addCapacity() {
            this.stats.capacity = this.stats.capacity + 1;
            this.deck.push({});
        },
        removeCapacity() {
            this.stats.capacity = this.stats.capacity - 1;
            this.deck.splice(this.deck.length - 1, 1);
        },
        getWand(id) {
            return Object.assign({}, this.wands[id]);
        },
        getSpell(i) {
            if (i == -1) {
                return {};
            } else {
                return Object.assign({}, this.spells[i]);
            }
        },
        removeAlwaysCast() {
            this.alwaysCast = [{}];
        },
        removeSpell(index) {
            this.$set(this.deck, index, {});
        },
        selectWand(value) {
            let wand = Object.assign({}, value);
            this.stats.sprite = wand;
            this.selectingWand = false;
        },
        save() {
            this.fakeSaving = true;
            let wands = localStorage.getItem("wands");
            if (typeof wands == "string") {
                wands = JSON.parse(wands);
            } else {
                wands = [];
            }
            wands.push({
                stats: this.stats,
                alwaysCast: this.alwaysCast,
                deck: this.deck
            });
            localStorage.setItem("wands", JSON.stringify(wands));
            setTimeout(() => {
                this.fakeSaving = false;
                this.saving = false;
                this.$buefy.toast.open("Saved to presets.");
            }, 500);
        },
        clear() {
            this.stats = {
                sprite: {},
                shuffle: false,
                spellsCast: 1,
                speed: 1,
                cast: 0.1,
                recharge: 0.1,
                spread: 0,
                mana: 1300,
                mana_recharge: 280,
                capacity: 20
            };
            this.alwaysCast = [{}];
            let deck = [];
            for (let i = 0; i < this.stats.capacity; i++) {
                deck.push({});
            }
            this.deck = deck;
        }
    }
};
</script>