<template>
    <div class="box">
        <b-tabs v-model="spellCategory">
            <b-field>
                <b-input placeholder="Search..." v-model="filterKey" type="search" icon="magnify"></b-input>
            </b-field>
            <b-tab-item label="Projectiles"></b-tab-item>
            <b-tab-item label="Static"></b-tab-item>
            <b-tab-item label="Modifiers"></b-tab-item>
            <b-tab-item label="Multicast"></b-tab-item>
            <b-tab-item label="Materials"></b-tab-item>
            <b-tab-item label="Other"></b-tab-item>
            <b-tab-item label="Utility"></b-tab-item>
            <b-tab-item label="Passive"></b-tab-item>
        </b-tabs>
        <draggable v-model="filteredSpells" :group="spellsGroup" :sort="false" :swap="true">
            <div @dblclick="addSpell(val)" class="picker" v-for="val in filteredSpells" :key="val.id" :data="val">
                <SpellSlot :value="val" />
            </div>
        </draggable>
    </div>
</template>

<script>
import draggable from "vuedraggable";
import spellData from "../assets/spellData.json";
import SpellSlot from "./SpellSlot.vue";
export default {
    components: {
        draggable,
        SpellSlot
    },
    data() {
        return {
            spellsGroup: { name: "spells", put: false },
            spells: spellData,
            spellCategory: 0,
            filterKey: ""
        };
    },
    computed: {
        filteredSpells: {
            get() {
                let data = this.spells;
                let filterKey = this.filterKey.toLowerCase()
                data = data.filter(v => {
                    return v.type == this.spellCategory;
                }).filter(v => {
                    return v.name.toLowerCase().indexOf(filterKey) > -1
                })
                
                return data;
            },
            set() {
                return false;
            }
        }
    },
    methods: {
        addSpell(val) {
            this.$root.$emit("addSpell", val)
        }
    }
};
</script>

<style scoped lang="scss">
.picker {
    display: inline-block;
}
$white: hsl(217, 71%, 53%);
$primary: $white;
</style>